import Cookies from 'js-cookie'
import Color from 'css-color-function'
import formula from '@/styles/formula.json'

export const setToken = (token) => {
  return Cookies.set('TOKEN', token)
}
export const getToken = () => {
  return Cookies.get('TOKEN')
}

export const convertData = (target, hash) => {
  Object.keys(hash).forEach((key) => {
    if (target[key] !== undefined) {
      target[hash[key]] = target[key]
      delete target[key]
    }
  })
}

export const setColor = (color) => {
  let colors = {primary: color}
  Object.keys(formula).forEach(key => {
    const value = formula[key].replace(/primary/g, color)
    colors[key] = Color.convert(value)
  })
  //
  let cssText = window.cssText
  if (cssText) {
    Object.keys(colors).forEach(key => {
      cssText = cssText.replace(new RegExp('(:|\\s+)' + key, 'g'), '$1' + colors[key])
    })
    document.getElementById('style-css').innerText = cssText
  }
}



