<template>
  <div class="layout-container">
    <div class="layout-menu shade-6-background" :class="{'is-spread':isSpread}">
      <button class="menu-button light-8-color primary-color-hover" :class="{'is-spread':isSpread}"
              @click="handleSpread">
        <i class="iconfont gym-icon-spread"></i>
      </button>
      <ul class="menu-list light-8-color">
        <li
          v-for="menu in menus"
          :key="menu.key"
          class="primary-color-hover"
          :class="{'primary-color':$route.path.indexOf('/'+menu.key)===0}"
          @click="handleClick(menu.key)"
        >
          <i :class="'iconfont gym-icon-'+menu.key"></i>
          <CollapseTransition>
            <p class="menu-title" v-show="isSpread">{{menu.name}}</p>
          </CollapseTransition>
          <i v-if="menu.key==='show'&&approvalNum" class="alert-dot primary-background">{{approvalNum}}</i>
        </li>
      </ul>
      <div class="color-btn menu-button light-8-color primary-color-hover" style="position: relative"
           @click="handleSelect" ref="color" tabindex="0" @blur="selectingColor=false">
        <i class="iconfont gym-icon-palette"></i>
        <CollapseTransition>
          <p class="menu-title" v-show="isSpread"></p>
        </CollapseTransition>
        <transition name="el-zoom-in-bottom">
          <div class="colors-box" :class="{'active-color':isSpread}" v-show="selectingColor" @click.stop>
            <ul class="colors-list">
              <li v-for="(color,i) in colorOption" :key="i" :style="'background: '+color"
                  @click="handleChange(color)"></li>
            </ul>
          </div>
        </transition>
      </div>
    </div>
    <div class="layout-body">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
import 'styles/iconfont/iconfont.css'
import CollapseTransition from 'element-ui/lib/transitions/collapse-transition'
import { setColor } from 'utils/tool'
import { saveTheme } from 'api/setting'

export default {
  name: 'Layout',
  components: {CollapseTransition},
  data () {
    return {
      menus: [
        {key: 'show', name: ''},
        {key: 'wall', name: ''},
        {key: 'schedule', name: ''},
        {key: 'screen', name: ''},
        {key: 'experience', name: ''},
        {key: 'manage', name: ''},
        {key: 'setting', name: ''}
      ],
      isSpread: false,
      colorOption: [
        '#6494DD', '#25A5EC', '#4054B0', '#F1433A', '#E72366',
        '#9827A7', '#6D3FB9', '#1A988D', '#42B04B', '#FA521F'
      ],
      selectingColor: false
    }
  },
  computed: {
    approvalNum: function () {
      return this.$store.state.approval_num
    }
  },
  methods: {
    handleClick (key) {
      this.$router.push({path: '/' + key})
    },
    handleSpread () {
      this.isSpread = !this.isSpread
    },
    handleSelect () {
      if (this.selectingColor) {
        this.selectingColor = false
      } else {
        this.selectingColor = true
        if (this.$refs.color && (typeof this.$refs.color.focus === 'function')) {
          this.$refs.color.focus()
        }
      }
    },
    handleChange (color) {
      setColor(color)
      saveTheme({theme: color}).then(res => {
        if (Number(res.code) === 200) {
          this.$message({
            message: '',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.message,
            type: 'success'
          })
        }
      })
    }
  }
}
</script>
<style lang='stylus' scoped>
  .layout-container
    width 100%
    height 100%
    display flex
    flex-direction row
    align-items stretch
  .layout-menu
    display flex
    flex-direction column
    align-items stretch
    transition: width 0.2s;
    width 50px
    padding-top 16px
    padding-bottom 20px
    &.is-spread
      width 100px
  .menu-button
    padding 10px 0
    background none
    transition: transform 0.2s;
    &.is-spread
      transform: rotate(90deg);
    & > .iconfont
      font-size 24px
  .layout-body
    flex 1
    background #E8ECF0
    overflow hidden
    position relative
  .menu-list
    width 100%
    flex 1
    display flex
    flex-direction column
    align-items stretch
    & > li
      padding 6px 0
      display flex
      flex-direction column
      align-items center
      justify-content center
      cursor pointer
      position relative
    .iconfont
      font-size 24px
  .menu-title
    word-break keep-all
  .colors-box
    position absolute
    left 10px
    bottom 48px
    width 124px
    padding 10px
    padding-bottom 4px
    background white
    z-index 100
    border-radius 4px
    box-shadow 0 0 6px 0 rgba(102, 102, 102, 0.2)
    &.active-color
      bottom 70px
    &:before
      content ''
      position absolute
      bottom -6px
      left 8px
      width 0
      height 0
      border-top 6px solid white
      border-left 6px solid transparent
      border-right 6px solid transparent
    &.active-color:before
      left 34px
  .colors-list
    display flex
    flex-wrap wrap
    margin-right -10px
    overflow hidden
    & > li
      width 12px
      height 12px
      border-radius 50%
      margin-right 10px
      margin-bottom 6px
  .color-btn
    text-align center
    cursor pointer
    outline none
  .alert-dot
    width 20px
    height 20px
    color white
    border-radius 50%
    position absolute
    top 0
    right 6px
    font-size 12px
    font-style unset
    display flex
    justify-content center
    align-items center
    transition right .2s
  .is-spread .alert-dot
    right 16px
</style>



