// 
export const formatStamp = (stamp, format) => {
  function repairZero (num) {
    num = String(num)
    return num.length === 1 ? '0' + num : num
  }

  let date = new Date(stamp)
  if (date.getTime()) {
    format = format || 'YYYY-MM-DD hh:mm:ss'
    let year = date.getFullYear()
    let month = repairZero(date.getMonth() + 1)
    let day = repairZero(date.getDate())
    let hour = repairZero(date.getHours())
    let min = repairZero(date.getMinutes())
    let sec = repairZero(date.getSeconds())
    return format.split('YYYY').join(year).split('MM').join(month).split('DD').join(day)
      .split('hh').join(hour).split('mm').join(min).split('ss').join(sec)
  } else {
    return ''
  }
}

export const json = (json) => {
  return JSON.parse(JSON.stringify(json))
}

export const turnIntoJson = (json) => {
  try {
    return JSON.parse(json)
  } catch (e) {
    return false
  }
}

// 
if (process.env.NODE_ENV === 'development') {
  window.json = json
  window.log = log => console.log(log)
} else {
  window.json = () => {}
  window.log = () => {}
}

export default {
  formatStamp
}



