<template>
  <transition name="fade">
    <div class="dialog-container" v-show="visible">
      <div class="dialog-box" @click.stop v-loading="loading" :class="{'dialog-long-text':mode==='longText'}">
        <div class="dialog-header">
          <div class="dialog-icon"><i></i></div>
          <div class="dialog-title">{{title}}</div>
          <div class="dialog-close-button" @click.once="destroy">
            <i class="dialog-close-icon"></i>
          </div>
        </div>
        <div class="dialog-body">{{brief}}</div>
        <div class="dialog-footer">
          <button class="dialog-cancel" @click.once="handleCancel" v-if="type==='confirm'">{{cancelText}}</button>
          <button class="dialog-confirm" @click="handleConfirm">{{confirmText}}</button>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'dialog',
  data () {
    return {
      mode: false,
      visible: false,
      loading: false,
      type: 'alert',
      title: '',
      brief: '',
      confirm: '',
      cancel: false,
      confirmText: '',
      cancelText: ''
    }
  },
  methods: {
    handleConfirm () {
      if (typeof this.confirm === 'function') {
        this.confirm(this)
      }
    },
    handleCancel () {
      if (typeof this.cancel === 'function') {
        this.cancel(this)
      } else {
        this.destroy()
      }
    },
    destroy (callback) {
      this.visible = false
      setTimeout(() => {
        this.$destroy(true)
        this.$el.parentNode.removeChild(this.$el)
        if (typeof callback === 'function') {
          callback()
        }
      }, 500)
    }
  }
}
</script>
<style lang='stylus' scoped>
  .dialog-container
    position fixed
    top 0
    left 0
    width 100%
    height 100%
    background rgba(0, 0, 0, .2)
    display flex
    align-items center
    justify-content center
  .dialog-box
    width 520px
    min-height 160px
    background white
    border-radius 8px
    box-shadow 0 2px 8px 0 rgba(0, 0, 0, 0.2)
  .dialog-header
    height 80px
    display flex
    flex-direction row
    align-items center
  .dialog-icon
    width 110px
    padding-left 30px
    display flex
    flex-direction row
    align-items center
    & > i
      display inline-block
      background-size 100% 100%
      background url('~@/assets/modal_icon_1.png') no-repeat center
      width 50px
      height 40px
  .dialog-title
    flex 1
    color #333333
    font-size 16px
  .dialog-close-button
    padding 10px
    padding-right 30px
    display flex
    justify-content center
    align-items center
    cursor pointer
  .dialog-close-icon
    display inline-block
    background-size 100% 100%
    width 12px
    height 12px
    background url('~@/assets/blue_close.png') no-repeat center
  .dialog-body
    font-size 20px
    color #333333
    text-align center
  .dialog-footer
    padding-top 40px
    padding-bottom 30px
    text-align center
    & > button
      width 90px
      height 36px
      border-radius 4px
  .dialog-cancel
    border 1px solid #2162D2
    color #2162D2
    margin-right 30px
  .dialog-confirm
    background #2162D2
    color white
  /*long text*/
  .dialog-long-text .dialog-body
    font-size 16px
    text-align left
    padding-left 115px
    padding-right 63px
  .dialog-long-text .dialog-footer
    padding-left 115px
    text-align left
  .dialog-long-text .dialog-cancel
    margin-right 140px
</style>



