import request from 'utils/request'

export function getUrl (data) {
  return request({
    method: 'post',
    url: '/gym/appUrl/getAppUrl',
    data
  })
}

export function setUrl (data) {
  return request({
    method: 'post',
    url: '/gym/appUrl/saveAppUrl',
    data
  })
}

export function getTheme (data) {
  return request({
    timeout: 1000,
    method: 'post',
    url: '/gym/theme/getTheme',
    data
  })
}

export function saveTheme (data) {
  return request({
    method: 'post',
    url: '/gym/theme/saveTheme',
    data
  })
}



