import Layout from 'views/layout/Layout'
import ParentRoute from 'components/ParentRoute'
// 
let show = [{
  path: '/show/detail',
  component: () => import('views/show/Detail'),
  meta: {
    title: ''
  }
}, {
  path: '/show/detail/:id',
  component: () => import('views/show/Detail'),
  meta: {
    title: ''
  }
}, {
  path: '/show',
  name: 'Home',
  redirect: '/show/public',
  component: () => import('views/show/components/Tab'),
  children: [{
    path: '/show/public',
    component: ParentRoute,
    redirect: '/show/public/all',
    children: [{
      path: '/show/public/:type',
      component: () => import('views/show/List'),
      meta: {
        title: function (route) {
          switch (route.params.type) {
            case 'all':
              return ''
            case 'male':
              return ''
            case 'female':
              return ''
          }
        }
      }
    }]
  }, {
    path: '/show/approval',
    component: ParentRoute,
    redirect: '/show/approval/waiting',
    children: [{
      path: '/show/approval/:type',
      component: () => import('views/show/Approval'),
      meta: {
        title: function (route) {
          switch (route.params.type) {
            case 'waiting':
              return ''
            case 'finish':
              return ''
          }
        }
      }
    }]
  }]
}]
// 
let wall = [{
  path: '/wall',
  component: () => import('views/wall/List'),
  meta: {
    title: ''
  }
}, {
  path: '/wall/detail',
  component: () => import('views/wall/Detail'),
  meta: {
    title: ''
  }
}, {
  path: '/wall/detail/:id',
  component: () => import('views/wall/Detail'),
  meta: {
    title: ''
  }
}]
// 
let screen = [{
  path: '/screen',
  component: () => import('views/screen/components/Tab'),
  redirect: 'screen/top',
  children: [{
    path: ':type',
    component: () => import('views/screen/List'),
    meta: {
      title: function (route) {
        switch (route.params.type) {
          case 'top':
            return ''
          case 'middle':
            return ''
          case 'bottom':
            return ''
        }
      }
    }
  }, {
    path: ':type/folder/:id',
    component: () => import('views/screen/Folder'),
    meta: {
      title: ''
    }
  }]
}, {
  path: 'screen/:type/detail',
  component: () => import('views/screen/Detail'),
  meta: {
    title: function (route) {
      switch (route.params.type) {
        case 'top':
          return '-'
        case 'middle':
          return '-'
        case 'bottom':
          return '-'
      }
    }
  }
}, {
  path: 'screen/:type/detail/:id',
  component: () => import('views/screen/Detail'),
  meta: {
    title: ''
  }
}]
export default [
  {
    path: '/',
    component: () => import('views/Index'),
    children: [
      {
        path: '/',
        component: Layout,
        redirect: '/show',
        children: [
          {
            path: '/home',
            component: () => import('views/Home')
          },
          ...show,
          ...wall,
          {
            path: '/schedule',
            component: () => import('views/schedule/Index')
          },
          ...screen,
          {
            path: '/experience',
            component: () => import('views/experience/List')
          },
          {
            path: '/manage',
            component: () => import('views/manage/List')
          },
          {
            path: '/setting',
            redirect: '/setting/qr_code',
            component: () => import('views/setting/components/Tab'),
            children: [
              {
                path: 'qr_code',
                component: () => import('views/setting/Code'),
                meta: {
                  title: ''
                }
              },
              {
                path: 'bracelet',
                component: () => import('views/setting/Bracelet'),
                meta: {
                  title: ''
                }
              },
              {
                path: 'sign_in',
                component: () => import('views/setting/SignIn'),
                meta: {
                  title: ''
                }
              },
              {
                path: 'about',
                component: () => import('views/setting/About'),
                meta: {
                  title: ''
                }
              }
            ]
          }
        ]
      }
    ]
  },
  {
    name: 'Error',
    path: '/error',
    component: () => import('views/Error'),
    meta: {
      title: ''
    }
  },
  {
    name: 'Loading',
    path: '/loading',
    component: () => import('views/Loading'),
    meta: {
      title: ''
    }
  },
  {
    path: '/404',
    name: '404',
    component: () => import('views/404.vue'),
    meta: {
      title: '404'
    }
  },
  {
    path: '*',
    component: () => import('views/404.vue')
  }
]



