import { getApprovalNum } from 'api/show'
import { Message } from 'element-ui'

export default {
  getApprovalNum ({commit}) {
    getApprovalNum({}).then(res => {
      if (Number(res.code) === 200) {
        commit('SET_APPROVAL_NUM', Number(res.data))
      } else {
        Message.warning({message: res.message})
      }
    })
  }
}



