import request from 'utils/request'

// 
export function getData (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/listUserShowInfo',
    data
  })
}

export function addData (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/saveUserShow',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

export function getDataById (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/getUserShow',
    data
  })
}

export function updateDataById (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/updateUserShow',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

export function deleteDataById (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/delUserShow',
    data
  })
}

export function getApprovalData (data) {
  if (data['type'] === 'waiting') {
    delete ['type']
    return request({
      method: 'post',
      url: '/gym/userShow/getExamineInfo',
      data
    })
  } else if (data['type'] === 'finish') {
    delete ['type']
    return request({
      method: 'post',
      url: '/gym/userShow/getExamineAdoptInfo',
      data
    })
  }
}

export function setApprovalAgree (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/examineUserShow',
    data
  })
}

export function setApprovalRefuse (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/examineRefuseUserShow',
    data
  })
}

export function deleteApproval (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/delExamineAdoptInfo',
    data
  })
}

export function getApprovalNum (data) {
  return request({
    method: 'post',
    url: '/gym/userShow/getTipsInfoCount',
    data
  })
}



