import Vue from 'vue'
import component from './Dialog.vue'

const DialogConstructor = Vue.extend(component)
let nId = 1
export default {
  install: Vue => {
    Vue.prototype.$confirm = (option) => {
      let id = 'confirm-' + nId++
      const dialog = new DialogConstructor({
        data: Object.assign({type: 'confirm'}, option)
      })
      dialog.id = id
      dialog.vm = dialog.$mount()
      dialog.dom = dialog.vm.$el
      // show
      dialog.vm.visible = true
      dialog.dom.style.zIndex = nId + 1001
      document.body.appendChild(dialog.dom)
      return dialog.vm
    }
  }
}



